<template>
    <transition
        enter-active-class="animated fadeIn"
        leave-active-class="animated fadeOut" >
        <div v-show="config.show"
             class="modal flex flex-v-end" :class="config.theme"
             style="animation-duration: .5s"
             :style="{
                 zIndex:zIndex,
                 animationDuration: config.duration,
                 'animation-duration': config.duration
             }">
            <div class="modal-backdrop in show" @click="clickBack"></div>
            <transition
                enter-active-class="animated fadeInUp"
                leave-active-class="animated fadeOutDown" >
                <div v-show="config.show"
                     class="modal-dialog"
                     style="animation-duration: .5s"
                     :style="{width: config.width}">
                    <div class="modal-content">
                        <slot name="content">
                            <slot name="header">
                                <div class="modal-header">
                                    <slot name="title">
                                        <h4 class="modal-title">{{config.title}}</h4>
                                    </slot>
                                </div>
                            </slot>
                            <div class="modal-body">
                                <slot></slot>
                            </div>
                            <slot name="footer">
                                <div class="modal-footer">
                                    <button type="button" class="btn dark btn-outline" @click.stop="close()">关闭</button>
                                    <slot name="button"></slot>
                                </div>
                            </slot>
                        </slot>
                    </div>
                </div>
            </transition>
        </div>
    </transition>
</template>
<style type="text/css">
.modal{
    display: block;
}
</style>
<script type="text/javascript">

import { mapState, mapActions } from 'vuex';

export default {
    props: {
        config: {
            default: function(){
                return {
                    show: false,
                    title: '',
                    hideOnClickOut: true,
                    theme: '',
                    duration: 1,
                    position: 'center'  //left center right top center bottom
                }
            }
        }
    },
    data() {
        return {
            zIndex: 0
        }
    },
    computed: mapState([
        'maxZIndex'
    ]),
    methods: {
        ...mapActions([
            'increaseZIndex'
        ]),
        close(){
            this.config.show = false;
        },
        clickBack() {
            if(this.config.hideOnClickOut) {
                this.config.show = false;
            }
        }
    },
    watch: {
        'config.show': function(val, oldVal){
            if(val === true) {
                this.increaseZIndex();
                this.zIndex = this.maxZIndex;
            }
        }
    }
}
</script>



