import $ from 'jquery';
import layer from './assets/js/layer/layer';

export default {
    thumb: (url, width, height, quality) => {
        width = width || 200
        height = height || 200
        quality = quality || 50
        return `${url}?imageView/2/w/${width}/h/${height}/q/${quality}/format/png`
    },
    
    alert: (msg, title, ok) => {
        let al = layer.alert( msg, {
            btn:[ '确定' ]
        }, function () {
            layer.close(al);
            ok && ok();
        });

        return al;
    },
    confirm: (msg, ok, cancel) => {
        
        var mconfirm = layer.confirm( msg, {
            btn:['确定','取消']
        }, function () {
            layer.close(mconfirm);
            ok && ok();
        },
        function () {
            layer.close(mconfirm);
            cancel && cancel();
        });
        
        return mconfirm;
    },
    toast: (msg, timeout, cb) => {
        
        let time, callback;

        if($.isNumeric(timeout)) {
            time = timeout;
        }
        
        if($.isFunction(cb)) {
            callback = cb;
        } else if($.isFunction(timeout)) {
            callback = timeout;
        }
        var mtoast = layer.msg( msg, {
            time: time || 1000
        }, function(){
            callback && callback();
        });
        
        return mtoast;
    }
};



