<template>
    <div>
        <c-header class="header">
            <div slot="headerLeft">
                <router-link :to="{name: 'fdIndex'}"
                             class="btn btn-red">返回</router-link>
            </div>
            一键开门
        </c-header>
        <div class="result">
            <ul v-for="item in list">
                <li>{{item.name}}</li>
                <li>地点:{{item.classroom}}</li>
                <li>时间:{{item.date}}</li>
                <li v-if="seen">老师:{{item.teacher}}</li>
            </ul>
        </div>
        <div class="open">
            <div class="door_btn" @click="viewLab">
                <p>一键开门</p>
            </div>
        </div>
        <c-modal :config="labInfoModal">
            <ul>
                <li v-for="door in door_list" @click="">{{door}}</li>
            </ul>
        </c-modal>
    </div>
</template>

<script>
    import $ from 'jquery';
    import methods from '../../methods';
    import cHeader from '../header';
    import cModal from '../../components/modal';
    export default {
        data() {
            return {
                labInfoModal: {
                    show: false,
                    title: '选择你需要打开的门'
                },
                list: [
                    {
                        "name":"病理生理学",
                        "classroom":"临床技能中心308教室",
                        "date":"4月8号 9:00~12:00",
                        "teacher":"谭随老师"
                    },
                    {
                        "name":"病理生理学",
                        "classroom":"临床技能中心308教室",
                        "date":"4月8号 9:00~12:00",
                        "teacher":"谭随老师"
                    },
                    {
                        "name":"病理生理学",
                        "classroom":"临床技能中心308教室",
                        "date":"4月8号 9:00~12:00",
                        "teacher":"谭随老师"
                    },
                    {
                        "name":"病理生理学",
                        "classroom":"临床技能中心308教室",
                        "date":"4月8号 9:00~12:00",
                        "teacher":"谭随老师"
                    },
                    {
                        "name":"病理生理学",
                        "classroom":"临床技能中心308教室",
                        "date":"4月8号 9:00~12:00",
                        "teacher":"谭随老师"
                    },
                    {
                        "name":"病理生理学",
                        "classroom":"临床技能中心308教室",
                        "date":"4月8号 9:00~12:00",
                        "teacher":"谭随老师"
                    }
                ],
                door_list:[],
                seen:true,
            }
        },
        components: {
            cHeader,
            cModal
        },
        created() {
        },
        mounted () {
            this.queryView();
        },
        methods: {
            queryView(){
                $.ajax({
                    url:"",
                    type:"POST",
                    data:{},
                    dataType:"json",
                    success:function(data){
                        _this.list=data;
                    },
                    error:function(){

                    }
                });
            },
            open(val){
                var _this=this;
                layer.load(1, {//loading
                    shade: [0.5,'#fff']
                });
                $.ajax({
                    url:"",
                    type:"POST",
                    data:{},
                    dataType:"json",
                    success:function(data){
                        _this.$router.go({
                            name: ''
                        });
                    },
                    error:function(err){
                        if(err==1){
                            alert("操作失败")
                        }else if(err==2){
                            alert("你当前无法使用")
                        }else if(err==3){
                            alert("网络不大给力")
                        }
                    }
                });
            },
            viewLab(){
                var _this=this;
                $.ajax({
                    url:"",
                    type:"POST",
                    data:{},
                    dataType:"json",
                    success:function(data){
                        _this.door_list=data;
                        this.labInfoModal.show = true;
                    },
                    error:function() {

                    }
                });
            }
        }
    }

</script>
<style  lang="scss">
    .result {
        padding-bottom: 4rem;
        ul {
            background:#fff;
            padding:0.3rem;
            margin-bottom:0.1rem;
            li {
                line-height:0.4rem;
                font-size:0.3rem;
            }
        }
    }
    .open {
        background: #eee;
        width:100%;
        position: fixed;
        bottom:0;
        .door_btn {
            width:2rem;
            height:2rem;
            margin:1rem auto;
            color:#fff;
            background: url("../../assets/images/signbtns.png") 0 0 no-repeat;
            background-size: cover;
            p {
                display: inline-block;
                position: relative;
                top: 50%;
                left:50%;
                transform: translate(-50%,-50%);
                font-size: 0.35rem;
            }
        }
    }
</style>



