var xiefn={
    errcodefn:function(){
        if(res.errcode==9904){
            layer.msg("登录信息已失效，请重新登录");
            setTimeout(this.futureAppLogout,2000)
        }else{
            layer.msg(res.errdesc)
        }
    },
    errfn:function(){
        layer.msg("连接服务失败")
    },
    post:function(url,data,callback,errcodefn,errfn){
        data=JSON.stringify({ "1":data });
        var req=new XMLHttpRequest();
        req.open('POST',url);
        req.onreadystatechange=function(){
            if(req.readyState==4){
                if(req.status==200){
                    if(typeof(req.responseText)=="string"){
                        var response=JSON.parse(req.responseText)["1"];
                    }else{
                        var response=req.responseText["1"]
                    };
                    if(response.errcode==0){
                        callback(response)
                    }else{
                        errcodefn(response)
                    }
                }else{
                    errfn()
                }
            }
        };
        req.send(data)
    },
    futureAppLogout:function(){//调用外部方法返回登录界面
        if(navigator.userAgent.indexOf("Android")!=-1||navigator.userAgent.indexOf('Linux')!= -1){
            window.JavaInterface.JavaScriptLogout(); //callback android OS logout function
        } else{
            // callback ios OS function
            this.setupWebViewJavascriptBridge(function(bridge) {
                bridge.callHandler('Logout', {}, function responseCallback(responseData) {});
            });
        }
    },
    setupWebViewJavascriptBridge:function(callback){//ios桥函数
        if (window.WebViewJavascriptBridge) { return callback(WebViewJavascriptBridge); }
        if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(callback); }
        window.WVJBCallbacks = [callback];
        var WVJBIframe = document.createElement('iframe');
        WVJBIframe.style.display = 'none';
        WVJBIframe.src = 'wvjbscheme://__BRIDGE_LOADED__';
        document.documentElement.appendChild(WVJBIframe);
        setTimeout(function() { document.documentElement.removeChild(WVJBIframe) }, 0)
    },
    getMyAppointInfo:function(type){//我的预约查询接口
        var data={
            command:'learn/queryapplyinfo',
            sessionid:this.getCookie('sid'),
            loginid:this.getCookie('uid'),
            status:type,//0：待审批，1：待训练，2，不通过，3，取消
            teacherid:this.getCookie('uid')
        };
        return function callback(res){
            return res.applylist
        };
        post('/learn/queryapplyinfo',data,callback,this.errcodefn,this.errfn)
    },
    cancelMyAppoint:function(id){//我的预约取消预约接口
        var data={
            command:'learn/cancelapplybyteacher',
            sessionid:this.getCookie('sid'),
            loginid:this.getCookie('uid'),
            applyid:id,
            teacherid:this.getCookie('uid')
        };
        function callback(res){
            layer.msg('已为您取消一个预约')
        };
        post('/learn/cancelapplybyteacher',data,callback,this.errcodefn,this.errfn)
    },
    setCookie:function(name,value) {//设置cookie
        value=encodeURIComponent(value);
        document.cookie=name+"="+value;
    },
    getCookie:function(name){//获取cookie
        var arr = document.cookie.split("; ");
        for (var i = 0 ; i<arr.length; i++) {
            var _arr = arr[i].split("=");
            if (_arr[0]==name) {
                return decodeURIComponent(_arr[1]);
            }
        }
        return "";
    },
    confirmWifi:function(){//获取wifi信息函数
        if(navigator.userAgent.indexOf("iPhone")!=-1){//如果是苹果系统，保存登录信息,保存wifi状态
            setupWebViewJavascriptBridge(function(bridge) {
                bridge.callHandler('GetWiFiStatus', {'WIFIStatus': 'outSend'}, function responseCallback(responseData) {
                    currentWiFi=JSON.parse(responseData).currentWiFi;
                    designWiFi=JSON.parse(responseData).designWiFi;
                })
            })
        }else {
            result=window.JavaInterface.JavaScriptGetWiFiStatus("GetWiFiStatus");
            currentWiFi= JSON.parse(result).currentWiFi;//如果是GMC,data就等于GMC
            designWiFi=JSON.parse(result).designWiFi;
        }
    }
};
export default xiefn



