<template>
  <div class="bootbox modal" :style="{zIndex:zIndex}" v-if="alert.show" transition="fade">
    <div class="modal-backdrop in"></div>
    <div class="modal-dialog modal-tip-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="bootbox-close-button close" @click="alert.show = false">×</button>
          <h4 class="modal-title">
            <template v-if="alert.title">{{alert.title}}</template>
            <template v-else>提示</template>
          </h4>
        </div>
        <div class="modal-body">
          <div class="bootbox-body">{{alert.msg}}</div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-primary" @click.stop="ok()"> 确定 </button>
        </div>
      </div>
    </div>
  </div>
</template>
<style></style>
<script>

  import { mapState, mapActions } from 'vuex';

  export default {
    data(){
      return {
        zIndex: 0
      };
    },
    computed: mapState([
      'alert',
      'maxZIndex'
    ]),
    methods: {
      ...mapActions([
        'hideAlert',
        'increaseZIndex'
      ]),
      ok: function(){
        this.alert && this.alert.ok && this.alert.ok();
        this.hideAlert();
      }
    },
    watch: {
      'alert.show': function(val, oldVal){
        if(val === true) {
          this.zIndex = this.maxZIndex;
          this.increaseZIndex();
        }
      }
    }
  }
</script>



