<template>
    <div>
        <div class="contentBox" v-for="(value,index) in finishedList">
            <dl>
                <dt class="boxTitle">实验室名称:
                    <span>{{ value.placename }}</span>
                </dt>
                <dd>
                    实验室地址:
                    <span>{{ value.placeaddress }}</span>
                </dd>
                <dd>
                    管理员:
                    <span>{{ value.admin }},{{ value.mobile }}</span>
                </dd>
                <dd>
                    训练项名称:
                    <span v-for="(value2,index2) in finishedList[index].learnlist">{{ value2.learnname }}&nbsp;&nbsp;</span>
                </dd>
                <dd>
                    预约人数:
                    <span>{{ value.stunum }}</span>
                </dd>
                <dd>
                    预约时段:
                    <span>{{ value.starttime }}~{{ value.endtime }}</span>
                </dd>
                <dd>
                    备注:
                    <span>{{ value.comment }}</span>
                </dd>
            </dl>
        </div>
    </div>
</template>
<script>
    import xieFn from '../../../vuex/xie'
    export default {
        data() {
            return {
                finishedList:'',
            }
        },
        mounted() {
            this.finishedList=xieFn.getMyAppointInfo(3)
        },
        methods:{

        }
    }
</script>




