<template>
    <div>
        <div class="contentBox" v-for="(value,index) in toCheckList">
            <dl>
                <dt class="boxTitle">实验室名称:
                    <span>{{ value.placename }}</span>
                </dt>
                <dd>
                    实验室地址:
                    <span>{{ value.placeaddress }}</span>
                </dd>
                <dd>
                    管理员:
                    <span>{{ value.admin }},{{ value.mobile }}</span>
                </dd>
                <dd>
                    训练项名称:
                    <span v-for="(value2,index2) in toCheckList[index].learnlist">{{ value2.learnname }}&nbsp;&nbsp;</span>
                </dd>
                <dd>
                    预约人数:
                    <span>{{ value.stunum }}</span>
                </dd>
                <dd>
                    预约时段:
                    <span>{{ value.starttime }}~{{ value.endtime }}</span>
                </dd>
                <dd>
                    备注:
                    <span>{{ value.comment }}</span>
                </dd>
            </dl>
            <p class="bottomHandlers">
                <router-link :to="{name: 'fdCreateSubmit',query:{ applyid:value.applyid }}" class="btn btn-white left">修改</router-link>
                <button class="btn btn-blue right" @click="open_shadow_cancel(value.applyid)">取消预约</button>
            </p>
        </div>
        <div class="shadow" v-show="isShadow">
            <div class="confirmBox" v-show="cancelBox">
                <p class="confirmText">
                    确定取消您的预约吗？
                </p>
                <p class="bottomHandlers">
                    <button class="btn btn-white right" @click="closeShadow()" style="margin-left:20px;">放弃</button>
                    <button class="btn btn-blue right" @click="cancelAppoint(cancelId)">确认</button>
                </p>
            </div>
        </div>
    </div>
</template>
<script>
    import xieFn from '../../../vuex/xie'
    export default {
        data() {
            return {
                isShadow:false,
                cancelBox:false,
                resetInfo:{},
                cancelInfo:{},
                toCheckList:'',
                cancelId:''
            }
        },
        mounted() {
            this.toCheckList=xieFn.getMyAppointInfo(0)
        },
        methods:{
            cancelAppoint:function(id){
                xieFn.cancelMyAppoint(id)
            },
            open_shadow_cancel:function(id){
                this.isShadow=true;
                this.cancelBox=true;
                this.cancelId=id;
            },
            closeShadow:function(){
                this.isShadow=false;
                this.cancelBox=false;
            }
        }
    }
</script>




