<template>
  <div class="bootbox modal" :style="{ zIndex: zIndex }" v-if="confirm.show" transition="fade">
    <div class="modal-backdrop in"></div>
    <div class="modal-dialog modal-tip-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="bootbox-close-button close" @click.stop="cancel()">×</button>
          <h4 class="modal-title">
            <template v-if="confirm.title">{{confirm.title}}</template>
            <template v-else>提示</template>
          </h4>
        </div>
        <div class="modal-body">
          <div class="bootbox-body">{{confirm.msg}}</div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-gray" @click.stop="cancel()"> 取消 </button>
          <button type="button" class="btn btn-primary" @click.stop="ok()"> 确定 </button>
        </div>
      </div>
    </div>
  </div>
</template>
<style></style>
<script>

  import { mapState, mapActions } from 'vuex';

  export default {

    data(){
      return {
        zIndex: 0
      };
    },
    computed: mapState([
      'confirm',
      'maxZIndex'
    ]),
    methods: {
      ...mapActions([
        'hideConfirm',
        'increaseZIndex'
      ]),
      ok: function(){
        this.confirm && this.confirm.ok && this.confirm.ok();
        this.cancel();
      },
      cancel: function() {
        this.confirm.show = false;
        this.hideConfirm();
      }
    },
    watch: {
      'confirm.show': function(val, oldVal){
        if(val === true) {
          this.zIndex = this.maxZIndex;
          this.increaseZIndex();
        }
      }
    }
  }
</script>



