<template>
    <div>
        <c-header>
            <div slot="headerLeft">
                <router-link :to="{name: 'fdCreate'}" class="header-back-btn">返回</router-link>
            </div>
            我的预约
        </c-header>
        <c-tabs :bus="bus">
            <c-tab title=" 待审批 " :active="type == 1 ? 'true': 'false'" :bus="bus">
                <to-check></to-check>
            </c-tab>
            <c-tab title=" 审批不通过 " :active="type == 2 ? 'true': 'false'" :bus="bus">
                <failed></failed>
            </c-tab>
            <c-tab title=" 待训练 " :active="type == 3 ? 'true': 'false'" :bus="bus">
                <to-finish></to-finish>
            </c-tab>
            <c-tab title=" 已完成 " :active="type == 4 ? 'true': 'false'" :bus="bus">
                <finished></finished>
            </c-tab>
        </c-tabs>
    </div>
</template>
<script>
    import Vue from 'vue';
    import cTabs from "../../../components/tabs";
    import cTab from "../../../components/tab";
    import cHeader from '../../header';
    import toCheck from './tocheck';
    import failed from './failed';
    import toFinish from './tofinish';
    import finished from './finished';

    let bus = new Vue();

    export default {
        data() {
            return {
                type: 2,
                bus: bus,
                list: [],
                list2: []
            }
        },
        route: {
            data: function(transition) {

            }
        },
        components: {
            cTabs,
            cTab,
            cHeader,
            toCheck,
            failed,
            toFinish,
            finished
        },
        created() {
            this.type = Number(this.$route.query.type || 2);
        },
        methods:{

        }
    }
</script>
<style>

</style>



