<template>
    <div class="booking-time-sel g-wrapper">
        <c-header>
            <div slot="headerLeft">
                <a @click="back()" class="header-back-btn">
                    <img src="../../../assets/images/icon_fanhui.png" />
                </a>
            </div>
            班级预约
        </c-header>

        <div class="time-sel">
            <div class="valign-center lab-info">
                <div class="auto-width">
                    综合穿刺虚拟教学实验室
                </div>
                <a class="view-lab" @click="viewLab()">查看实验室资源</a>
            </div>
            <!--<div class="tabs time-tabs" >
                <div tapmode="" class="tab active">
                    <label> 3.3 </label>
                    <div class="tab-current-bg"></div>
                </div>
                <div tapmode="" class="tab">
                    <label> 3.4 </label>
                </div>
                <div tapmode="" class="tab">
                    <label> 3.5 </label>
                </div>
                <div tapmode="" class="tab">
                    <label> 3.6 </label>
                </div>
                <div tapmode="" class="tab">
                    <label> 3.7 </label>
                </div>
                <div tapmode="" class="tab">
                    <label> 3.8 </label>
                </div>
                <div tapmode="" class="tab">
                    <label> 3.9 </label>
                </div>
            </div>-->

            <c-tabs :bus="bus" className="time-tabs" @change-tab="changeTab">
                <template v-for="it in items">
                    <c-tab :title="it.name" :active="it.active" :bus="bus">

                    </c-tab>
                </template>
            </c-tabs>
        </div>

        <div class="list list-cut lab-list g-main" v-infinite-scroll="loadMore">
            <template v-for="it in list">
                <div class="item">
                    <div class="valign-center">
                        <div class="auto-width">
                            <h3 class="name">{{it.time}}</h3>
                            <p class="addr">{{it.remain}} / {{it.total}}</p>
                        </div>
                        <a class="btn btn-blue" @click="toSubmit()" > 预约 </a>
                    </div>
                    <p>{{it.course}}</p>
                    <p>{{it.teacher}}</p>
                </div>
            </template>
        </div>

        <c-modal :config="labInfoModal">
            <ul>
                <li>hello</li>
                <li>world</li>
                <li>good</li>
            </ul>
        </c-modal>
    </div>
</template>

<script>

    import Vue from 'vue';
    import $ from 'jquery';
    import methods from '../../../methods';
    import cHeader from '../../header.vue';
    import cModal from '../../../components/modal';
    import cTabs from "../../../components/tabs";
    import cTab from "../../../components/tab";

    import { mapState, mapActions } from 'vuex';

    let bus = new Vue();

    export default {
        data() {
            return {
                labInfoModal: {
                    show: false,
                    title: '查看实验室资源'
                },
                bus: bus,
                currentTime: null,
                page: 1,
                hasMore: true,
                items: [{
                    name: '3.3',
                    active: true
                }, {
                    name: '3.4'
                }, {
                    name: '3.5'
                }, {
                    name: '3.6'
                }, {
                    name: '3.7'
                }, {
                    name: '3.8'
                }, {
                    name: '3.9'
                }],
                list: [{
                    time: '8:00-12:00',
                    remain: 30,
                    total: 40,
                    course: '啥都发发 安抚啊  阿斯顿发放',
                    teacher: '张三 李四 王二 麻子'
                } ,{
                    time: '8:00-12:00',
                    remain: 30,
                    total: 40,
                    course: '啥都发发 安抚啊  阿斯顿发放',
                    teacher: '张三 李四 王二 麻子'
                } ,{
                    time: '8:00-12:00',
                    remain: 30,
                    total: 40,
                    course: '啥都发发 安抚啊  阿斯顿发放',
                    teacher: '张三 李四 王二 麻子'
                } ,{
                    time: '8:00-12:00',
                    remain: 30,
                    total: 40,
                    course: '啥都发发 安抚啊  阿斯顿发放',
                    teacher: '张三 李四 王二 麻子'
                } ,{
                    time: '8:00-12:00',
                    remain: 30,
                    total: 40,
                    course: '啥都发发 安抚啊  阿斯顿发放',
                    teacher: '张三 李四 王二 麻子'
                }]
            }
        },
        components: {
            cHeader,
            cModal,
            cTabs,
            cTab
        },
        created() {},
        mounted () {},
        /*computed: mapState([
         'loginUser'
         ]),*/
        methods: {
            ...mapActions([
                'showAlert',
                'showLoading',
                'hideLoading',
                'toast'
            ]),
            ...methods,
            toSubmit(){
                this.$router.push({
                    name: 'fdCreateSubmit'
                });
            },
            back(){
                this.$router.push({
                    name: 'fdCreateSelLab'
                })
            },
            viewLab(){
                this.labInfoModal.show = true;
            },
            changeTab(tab, index) {
                this.currentTime = tab.time;
                this.page = 1;
                this.list = [];
                this.hasMore = true;
                this.loadData();
            },
            loadData(){
                // ajax
                this.list = [{
                    time: '8:00-12:00',
                    remain: 30,
                    total: 40,
                    course: '啥都发发 安抚啊  阿斯顿发放',
                    teacher: '张三 李四 王二 麻子'
                } ,{
                    time: '8:00-12:00',
                    remain: 30,
                    total: 40,
                    course: '啥都发发 安抚啊  阿斯顿发放',
                    teacher: '张三 李四 王二 麻子'
                }];
            },
            loadMore(){
                if(this.hasMore) {
                    this.page++;
                    this.loadData();
                }
            }
        }
    }

</script>
<style lang="scss">
    .booking-time-sel{
        border-top:0;

        .time-sel{
            background: #FFF;
            margin-bottom:.2rem;
            padding:.24rem;
        }
        .lab-info{
            font-size:.26rem;
            margin-bottom:.24rem;
            color:#333;

            .view-lab{
                font-size:.2rem;
                color:#369;
            }
        }
        .time-tabs{
            border:1px solid #3499db;
            border-radius: .06rem;
            padding-left:.18rem;
            padding-right:.18rem;

            .tab{
                label{
                    color: #888;
                    font-size:.3rem;
                    padding:.2rem .05rem .2rem .05rem;
                }
            }
        }
    }
</style>



