<script>

    import $ from 'jquery';
    import methods from '../../../methods';
    import cHeader from '../../header.vue';
    import cModal from '../../../components/modal';
    import { mapState, mapActions } from 'vuex';

    export default {
        data() {
            return {
                show: false,
                title: '我是一个Title',
                classModal: {
                    show: false,
                    title: '选择班级',
                    hideOnClickOut: true,
                    theme: 'hello-world'
