export default {
    CHANGE_MAIN_MENU(state, mainMenu) {
        state.mainMenu = mainMenu;
    },
    CHANGE_MENU_HIDE(state, menuHide) {
        state.menuHide = menuHide;
    },
    INCREASE_MAX_ZINDEX(state){
        return ++state.zIndex;
    },
    SHOW_ALERT(state, param) {

        state.alert = {
            show: true,
            msg: param.msg,
            title: param.tt,
            ok: param.ok
        };
    },
    HIDE_ALERT(state) {
        state.alert.show = false;
    },
    SHOW_CONFIRM(state, param) {
        state.confirm = {
            show: true,
            msg: param.msg,
            title: param.title,
            ok: param.ok,
            cancel: param.cancel
        }
    },
    HIDE_CONFIRM(state) {
        state.confirm.show = false;
    },
    SHOW_LOADING(state, param) {

        if(typeof param === 'string') {
            param = {
                msg: param
            };
        }
        state.loading = {
            show: true,
            msg: param.msg,
            html: param.html
        }
    },
    HIDE_LOADING(state) {
        state.loading.show = false;
    },
    SHOW_TOAST(state, param) {
        state.toast = {
            show: true,
            msg: param.msg,
            timeout: param.timeout || 1500
        }
    },
    HIDE_TOAST(state) {
        state.toast.show = false;
    }
}



