<template>
    <div class="loader">Hello, Welcome!</div>
</template>

<script>
export default {
    mounted(){},
    beforeRouteEnter (to, from, next) {
        next(vm => {
            setTimeout(() => {
                vm.$router.push({name: 'home', replace: true})
            }, 100)
        });
    }
}
</script>

<style scoped>
</style>



